						name: '',
						phone: '',
						address: ''
					}];
				} else {
					// 编辑
					this.showFields = true;
					this.FieldsTopName = '编辑单位';
					this.FieldsType = false;
					this.FieldsList = [{
						company: item.NAME,
						name: item.GROUPUSER,
						phone: item.PHONE,
						address: item.ADDRESS
					}];
					this.bianunitid = item.UNIT_ID;

				}
			},
			//确认新增或编辑父单位
			sureFields(type) {
				if (type == true) {
					//新增
					this.FieldsList.forEach(item => {
						if (item.company == '') {

							this.$refs.uToast.show({
								title: '单位名称不能为空',
								type: 'warning'
							})
							return;
						}

					})
					let fieldlist = JSON.stringify(this.FieldsList)
					let uid = "";
					uni.getStorage({
						key: 'USER_ID',
						success: function(res) {
							uid = res.data;
						}
					});
					helper.request(helper.websiteUrl + "v1/saveunit", {
						'uid': uid,
						'PARENT_ID': "",
						'FieldsList': fieldlist
					}, 'GET', false, (data) => {
						helper.showToast("新增成功！");
						this.FieldsList = [{
							company: '',
							name: '',
							phone: '',
							address: ''
						}];
						this.pageNumz = 1;
						this.fieldsName = "";
						this.initz();
						this.showFields = false;



					})


				} else {
					//编辑
					let fieldlist = JSON.stringify(this.FieldsList)
					let uid = "";
					uni.getStorage({
						key: 'USER_ID',
						success: function(res) {
							uid = res.data;
						}
					});
					helper.request(helper.websiteUrl + "v1/editunit", {
						'uid': uid,
						'UNIT_ID': this.bianunitid,
						'PARENT_ID': "",
						'FieldsList': fieldlist
					}, 'GET', false, (data) => {


						helper.showToast(data);
						this.FieldsList = [{
							company: '',
							name: '',
							phone: '',
							address: ''
						}];
						this.pageNumz = 1;
						this.fieldsName = "";
						this.initz();
						this.showFields = false;



					})






				}

			},
			//取消新增或编辑父单位
			cancelFields() {
				this.showFields = false;
			},
			//删除弹框
			delFields(item, index) {
				this.deleteunit = item;
				this.deleteunitindex = index;
				this.showFieldsdel = true;
			},
			//确认删除
			Fieldsdel() {

				let uid = "";
				uni.getStorage({
					key: 'USER_ID',
					success: function(res) {
						uid = res.data;
					}
				});
				helper.request(helper.websiteUrl + "v1/deleteunit", {
					'uid': uid,
					'UNIT_ID': this.deleteunit.UNIT_ID
				}, 'GET', false, (data) => {
					helper.showToast(data)
					this.collapseList.splice(this.deleteunitindex, 1)
					this.showChildel = false;
				})



			},

			//新增或编辑子单位弹框

			handleChild(item, index) {

				if (index == 1) {

					this.ChildList[0].parentcompany = item.NAME;
					this.ChildList[0].parentcompanyid = item.UNIT_ID;


					// 新增
					this.showChild = true;
					this.ChildTopName = '新增子单位';
					this.ChildType = true;
				} else if (index == 2) {

					this.ChildList = [{
						company: item.NAME,
						name: item.GROUPUSER,
						phone: item.PHONE,
						address: item.ADDRESS,
						parentcompany: this.parentcompany,
						parentcompanyid: ''
					}];
					this.bianunitid = item.UNIT_ID;


					// 编辑
					console.log("编辑")
					this.showChild = true;
					this.ChildTopName = '编辑子单位';
					this.ChildType = false;
				}
			},

			//确认新增或编辑父单位
			sureChild(type) {
				if (type == true) {
					//新增		
					let childlist = JSON.stringify(this.ChildList)
					let uid = "";
					uni.getStorage({
						key: 'USER_ID',
						success: function(res) {
							uid = res.data;
						}
					});
					helper.request(helper.websiteUrl + "v1/saveunitsecond", {
						'uid': uid,
						'FieldsList': childlist
					}, 'GET', false, (data) => {
						helper.showToast("新增成功！")
						this.secondunit = data
					})
					this.showChild = false;
				} else {
					//编辑	



					let fieldlist = JSON.stringify(this.ChildList)
					let uid = "";
					uni.getStorage({
						key: 'USER_ID',
						success: function(res) {
							uid = res.data;
						}
					});
					helper.request(helper.websiteUrl + "v1/editunitsecond", {
						'uid': uid,
						'UNIT_ID': this.bianunitid,
						'PARENT_ID': this.parentcompanyid,
						'FieldsList': fieldlist
					}, 'GET', false, (data) => {
						this.secondunit = data;
						//明天
						// this.FieldsList=[{
						// 	company: '',
						// 	name: '',
						// 	phone: '',
						// 	address: ''
						// }];
						// this.pageNumz=1;
						// this.fieldsName="";
						// this.initz(); 
						// this.showFields = false;



					})


					console.log("编辑")
					this.showChild = false;
				}

			},
			//取消新增或编辑父单位
			cancelChild() {




				this.showChild = false;
			},
			//删除弹框
			delChild(item, index) {
				this.deleteunit = item;
				this.deleteunitindex = index;
				this.showChildel = true;
			},
			//确认删除
			Childel() {

				let uid = "";
				uni.getStorage({
					key: 'USER_ID',
					success: function(res) {
						uid = res.data;
					}
				});
				helper.request(helper.websiteUrl + "v1/deleteunit", {
					'uid': uid,
					'UNIT_ID': this.deleteunit.UNIT_ID
				}, 'GET', false, (data) => {
					helper.showToast(data)
					this.secondunit.splice(this.deleteunitindex, 1)
					this.showChildel = false;
				})

			},
			// 字段配置部分end

		}
	}
</script>

